import React from 'react';
import PropTypes from 'prop-types';
import Box from '@material-ui/core/Box';
import ButtonBase from '@material-ui/core/ButtonBase';
import { makeStyles } from '@material-ui/styles';
import ProductInfo from './ProductInfo';

const useStyles = makeStyles(({ palette }) => ({
  buttonBase: {
    display: 'inline-block',
    height: '100%',
    width: '100%',
  },
  productImage: {
    position: 'relative',
    paddingBottom: '100%',
    paddingRight:'auto',
    paddingLeft:'auto',
    overflow: 'hidden',
   
  },
  img: {
    position: 'absolute',
    width: 'auto',
    height: '100%',
    top:'0',
    bottom:'0',
    left:'auto',
   objectFit:'contain'
    
  },
}));

const ProductCardFront = ({ product, bordered,inverted }) => {
  const classes = useStyles();
  
  return (
    <ButtonBase className={classes.buttonBase}  >
      <Box
        height={'100%'}
        {...bordered && {
          border: '1px solid #f0f0f0',
          borderTop: 'none',
          marginLeft: '-1px',
          marginTop: '-1px',
       
        }}
      >
        <Box p={2}>
          <div className={classes.productImage} style={{backgroundImage:'url('+product.image+')',backgroundSize:'contain',backgroundPosition:'center',backgroundRepeat:'no-repeat'}}>
          
          </div>
        </Box>
        <ProductInfo inverted={inverted}  name={product.title} price={product.price} />
      </Box>
    </ButtonBase>
  );
};

ProductCardFront.propTypes = {
  title: PropTypes.string.isRequired,
  price: PropTypes.number.isRequired,
  image: PropTypes.string,
  bordered: PropTypes.bool,
};
ProductCardFront.defaultProps = {
  image: '',
  bordered: false,
};

export default ProductCardFront;
