import React from 'react';
import PropTypes from 'prop-types';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '././extensions/Typography';

const CategoryPicker = ({ categories }) => (
  <List>
    {categories.map(({ active, title, amount }) => {
      const color = active ? 'primary' : 'textSecondary';
      return (
        <ListItem button key={title} dense>
          <ListItemText primary={title} primaryTypographyProps={{ color }} />
          <Typography variant={'body2'} color={color}>
            {amount}
          </Typography>
        </ListItem>
      );
    })}
  </List>
);

CategoryPicker.propTypes = {
  categories: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      amount: PropTypes.number,
    }),
  ),
};
CategoryPicker.defaultProps = {
  categories: [],
};
CategoryPicker.data = [
  {
    title: 'Green Product 1',
    amount: 56,
  },
  {
    title: 'Green Product 2',
    amount: 1,
    active: true,
  },
  {
    title: 'Green Product 3',
    amount: 23,
  },
  {
    title: 'Green Product 4',
    amount: 40,
  },
  {
    title: 'Green Product 5',
    amount: 11,
  },
  {
    title: 'Green Product 6',
    amount: 2,
  },
  {
    title: 'Green Product 7',
    amount: 8,
  },
  {
    title: 'Green Product 8',
    amount: 15,
  },
  {
    title: 'Green Product 9',
    amount: 14,
  },
];

export default CategoryPicker;
