import React, { useState, useRef } from 'react';
// import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';
import InputBase from '@material-ui/core/InputBase';
import Typography from '../extensions/Typography';
import Button from '../extensions/Button';
import Badge from '@material-ui/core/Badge';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
const useStyles = makeStyles(({ transitions }) => ({
  searchInput: ({ searchAppeared }) => ({
    width: searchAppeared ? 180 : 0,
    opacity: searchAppeared ? 1 : 0,
    transition: transitions.create(),
    backgroundColor: 'rgba(255, 255, 255, 0.08)',
    marginTop: 8,
    marginLeft: 8,
    padding: '0 8px',
    display: 'inline-block',
    color: '#ffffff',
  }),
  secondAppBar: ({ trigger }) => ({
    zIndex: 1099,
    transition: transitions.create('top', {
      duration: transitions.duration.leavingScreen,
      easing: transitions.easing.easeOut,
    }),
    top: trigger ? 0 : 64,
  }),
  appBarBg: {
    zIndex: 1098,
  },
}));

const AmiLargeHeader = ({cartLength}) => {
  const trigger = useScrollTrigger();
  const inputRef = useRef(null);
  const [searchAppeared, setSearchAppeared] = useState(false);
  const classes = useStyles({ searchAppeared, trigger });
  const handleSearchClick = () => {
    setSearchAppeared(!searchAppeared);
    if (!searchAppeared && inputRef.current) {
      inputRef.current.focus();
    }
  };
  return (
    <>
      
        <AppBar>
          <Toolbar>
            <Grid container wrap={'nowrap'} alignItems={'center'}>
            <Grid item xs container >
                <Typography variant={'h5'} style={{color:'white'}} weight={'900'} spacing={'big'}>
                  GREEN PLANET
                </Typography>
              </Grid>
              <Grid item xs>
                <Button
                  
                  className={'fa fa-search'}
                  shape={'circular'}
                  inverted
                  onClick={handleSearchClick}
                />
                <InputBase
                  inputRef={inputRef}
                  className={classes.searchInput}
                />
              </Grid>
              
              <Grid item xs>
                <Grid
                  container
                  alignItems={'center'}
                  justify={'flex-end'}
                  spacing={2}
                  wrap={'nowrap'}
                >
                  <Grid item>
                    <Button icon={'fa fa-user'} shape={'circular'} inverted />
                  </Grid>
                  
                  <Grid item>
                  <NavLink className="nav-link" to={"/cart"}><Badge  badgeContent={cartLength} color={'secondary'} >   <Button icon={'fa fa-shopping-cart'} shape={'circular'} inverted /></Badge></NavLink>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
    
      
    </>
  );
};

AmiLargeHeader.propTypes = {};
AmiLargeHeader.defaultProps = {};
const mapStateToProps = (state) => {
  return {
      cartLength: state.shop.cart.length
  }
};

export default connect(mapStateToProps, null)(AmiLargeHeader)
