import React from 'react';
import {connect} from 'react-redux';
import {formatMoney} from "../../pipes/priceFormatter";
import CartItem from "../../components/CartItem/CartItem";
import Button from "@material-ui/core/Button"
import { withRouter } from "react-router-dom";
import Hidden from "@material-ui/core/Hidden";

const ShoppingCart = (props) => {
 
   
        
    
    return (
        <>
       
                <div className="container" style={{paddingTop: '60px'}}>
                    <div className="card shopping-cart" style={{minWidth:'80vw'}}>
                        <div className="card-header  ">
                            <i className="fa fa-shopping-cart pr-2" style={{color:'black'}} aria-hidden="true"></i>
                            Shipping cart
                            <div className="clearfix"></div>
                        </div>
                        <div className="card-body">
                            {props.cartItemCount ? props.cartItems.map(cart => (
                                <CartItem {...cart} img={cart.image} />
                            )) : <h3 className=" text-center">There is no product in your cart</h3> }
                        </div>
                        <div className="card-footer">
                            <div className="pull-right" style={{margin: '10px'}}>
                                <div className="pull-right" style={{margin: '5px'}}>
                                   <p style={{textAlign:'right'}}>Total price: <i class="fa fa-inr"></i> <b>{formatMoney(props.totalPrice)}</b></p> 
                                    <br/>
                                    <br/>
                                    <Hidden smDown>
                                    <Button variant="contained" color="primary" style={{color:'white',padding:'10px',paddingRight:'50px',paddingLeft:'50px',marginRight:'20px'}} onClick={()=>{props.history.push('./products')}} >
        CONTINUE SHOPPING
      </Button>
                                    <Button variant="contained" color="primary" style={{color:'white',padding:'10px',paddingRight:'50px',paddingLeft:'50px'}} >
        CHECKOUT
      </Button>
      </Hidden>
      <Hidden smUp>
                                    <Button variant="contained" color="primary" style={{color:'white',padding:'10px',width:'90%',margin:'10px'}} onClick={()=>{props.history.push('./products')}} >
        CONTINUE SHOPPING
      </Button>
                                    <Button variant="contained" color="secondary" style={{color:'white',padding:'10px',width:'90%',margin:'10px'}} >
        CHECKOUT
      </Button>
      </Hidden>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
              
            </>
    );
};


const mapStateToProps = state => {

    console.log(state, 'state has changed');

    return {
        cartItems: state.shop.cart,
        cartItemCount: state.shop.cart.reduce((count, curItem) => {
            return count + curItem.quantity;
        }, 0),
        totalPrice: state.shop.cart.reduce((count, curItem) => {
            return count + (curItem.price * curItem.quantity);
        }, 0)
    }
}

export default withRouter(connect(mapStateToProps, null)(ShoppingCart));
