import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import Box from '@material-ui/core/Box';
import Typography from './extensions/Typography';
import Button from './extensions/Button';

const useStyles = makeStyles(({ palette, spacing }) => ({
  bg: ({ bgColor }) => ({
    overflow: 'hidden',
    ...(bgColor === 'purple1' && {
      backgroundColor: '#8BC34A',
    }),
    ...(bgColor === 'purple2' && {
      backgroundColor: '#4CAF50',
    }),
  }),
  tag: {
    background: palette.common.white,
    padding: spacing(0, 1),
  },
  overline: {
    lineHeight: 1,
    color: 'rgba(255, 255, 255, 0.87)',
  },
  image: ({ contentSide, imageCentered }) => ({
    maxWidth: '60%',
    position: 'absolute',
    zIndex: 0,
    bottom: '10px',

    height:'90%',
    width:'auto',
    objectFit:'contain',
    filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.4))',
    ...(imageCentered && {
      top: '50%',
      transform: 'translateY(-50%)',
    }),
    ...(contentSide === 'left'
      ? {
          right: 0,
        }
      : {
          left: 0,
        }),
  }),
}));

const ProductAds = ({
  title,
  overline,
  tag,
  image,
  imageCentered,
  actionLabel,
  bgColor,
  contentSide,
  rootBoxProps,
  contentBoxProps,
}) => {
  const classes = useStyles({ bgColor, contentSide, imageCentered });
  const sides = {
    left: 'left',
    right: 'right',
  };
  return (
    <Box
      position={'relative'}
      classes={{ root: classes.bg }}
      py={3}
      px={{ xs: 2, md: 3 }}
      height={'100%'}
      {...rootBoxProps}
    >
      <Box
        textAlign={sides[contentSide]}
        position={'relative'}
        zIndex={1}
        {...contentSide === 'right' && { ml: 'auto' }}
        {...contentBoxProps}
      >
        {tag && (
          <Typography
            className={classes.tag}
            display={'inline-block'}
            bottomSpace={'medium'}
            weight={'bold'}
          >
            {tag}
          </Typography>
        )}
        {overline && (
          <Typography
            className={classes.overline}
            gutterBottom
            variant={'overline'}
            display={'block'}
            weight={'bold'}
          >
            {overline}
          </Typography>
        )}
        <Typography
          bottomSpace={'medium'}
          inverted
          secondFamily
          weight={'bold'}
          variant={'h5'}
        >
          {title}
        </Typography>
        <Button shape={'chubby'} variant={'outlined'} inverted>
          {actionLabel}
        </Button>
      </Box>
      <img className={classes.image} alt={'product'} src={image} />
    </Box>
  );
};

ProductAds.propTypes = {
  title: PropTypes.string.isRequired,
  overline: PropTypes.string,
  tag: PropTypes.string,
  image: PropTypes.string,
  imageCentered: PropTypes.bool,
  actionLabel: PropTypes.string,
  bgColor: PropTypes.string,
  contentSide: PropTypes.oneOf(['right', 'left']),
  rootBoxProps: PropTypes.shape({}),
  contentBoxProps: PropTypes.shape({}),
};
ProductAds.defaultProps = {
  overline: '',
  tag: '',
  image: '',
  imageCentered: false,
  actionLabel: 'MORE',
  bgColor: '',
  contentSide: 'left',
  rootBoxProps: {},
  contentBoxProps: {},
};
ProductAds.test1 = {
  tag: 'SALE',
  overline: 'Mahadhan City compost',
  title: 'Green Sale 2019',
  actionLabel: 'MORE',
  bgColor: 'purple1',
  image:
    // eslint-disable-next-line max-len
    'https://slorge.s3.us-east-2.amazonaws.com/5iZh3havWV4SNAeNm7mUvgSa5.png',
};
ProductAds.test2 = {
  tag: 'NEW',
  overline: 'Botanix',
  title: 'PREMIUM FERTILIZER',
  actionLabel: 'SHOP NOW',
  bgColor: 'purple2',
  imageCentered: true,
  image:
    // eslint-disable-next-line max-len
    'https://slorge.s3.us-east-2.amazonaws.com/XdHufh3GcDxPzAeXWw9tk1mYf.png',
};

export default ProductAds;
